___CSS_LOADER_EXPORT___.push([module.id, ".bulle-container[data-v-d1f88520]{background-color:#fff;border-radius:8px;box-shadow:0 2px 15px #d3d3d3;padding:20px}.titre[data-v-d1f88520]{color:#343a40;font-size:1.5rem}.competences-container[data-v-d1f88520]{display:flex;flex-wrap:wrap;gap:10px;margin-top:15px}.competence-button[data-v-d1f88520]{background-color:#e3f2fd;border:1px solid #4caf50;border-radius:5px;color:#06c;cursor:pointer;font-size:1rem;padding:10px 20px;text-align:center;text-decoration:none;transition:background-color .3s,transform .2s}.competence-button[data-v-d1f88520]:hover{background-color:#4caf50;color:#fff;transform:scale(1.05)}ul[data-v-d1f88520]{list-style:none;padding:0}ul li[data-v-d1f88520]{background-color:#f8f9fa;border-radius:4px;margin:5px 0;padding:5px}", "",{"version":3,"sources":["webpack://./resources/js/components/layouts/TemplateProjet.vue"],"names":[],"mappings":"AAyEA,kCACA,qBAAA,CAGA,iBAAA,CAFA,6BAAA,CACA,YAEA,CAEA,wBAEA,aAAA,CADA,gBAEA,CAEA,wCACA,YAAA,CACA,cAAA,CACA,QAAA,CACA,eACA,CAEA,oCAIA,wBAAA,CAFA,wBAAA,CACA,iBAAA,CAEA,UAAA,CAEA,cAAA,CADA,cAAA,CALA,iBAAA,CAQA,iBAAA,CACA,oBAAA,CAFA,6CAGA,CAEA,0CACA,wBAAA,CACA,UAAA,CACA,qBACA,CAEA,oBACA,eAAA,CACA,SACA,CAEA,uBAGA,wBAAA,CACA,iBAAA,CAHA,YAAA,CACA,WAGA","sourcesContent":["<template>\n  <div v-if=\"projet\" class=\"container-fluid mt-3\">\n    <h1>{{ projet.name }}</h1>\n    <p>Description du projet :</p>\n    <div class=\"mb-2 bulle-container\">\n      <p v-html=\"projet.description\"></p>\n    </div>\n\n    <h2 class=\"mt-4\">Compétences Associées :</h2>\n    <div class=\"competences-container\">\n      <a v-for=\"(competence, index) in competencesLiees\" :key=\"index\" class=\"competence-button\"\n        :href=\"`/fiche_competence/${competence.id}`\">\n        {{ competence.titre }}\n      </a>\n    </div>\n  </div>\n  <div v-else>\n    <p>Chargement...</p>\n  </div>\n</template>\n\n<script>\nimport axios from \"axios\";\n\nexport default {\n  props: {\n    projet: {\n      type: Object,\n      required: true,\n    },\n  },\n  data() {\n    return {\n      competences: [], // Liste des compétences récupérées\n    };\n  },\n  computed: {\n    competencesLiees() {\n      // Vérifie si competence_id est un tableau ou une chaîne nécessitant un traitement\n      const competenceIds = Array.isArray(this.projet.competence_id)\n        ? this.projet.competence_id\n        : String(this.projet.competence_id)\n          .replace(/[{}]/g, \"\") // Supprimer les accolades\n          .split(\",\") // Diviser en tableau\n          .map((id) => parseInt(id.trim(), 10)) // Convertir en nombres\n          .filter((id) => !isNaN(id)); // Retirer les valeurs invalides\n\n      return competenceIds\n        .map((id) =>\n          this.competences.find((competence) => competence.id === id)\n        )\n        .filter((competence) => competence); // Exclure les compétences non trouvées\n    },\n  },\n  methods: {\n    // Récupère les compétences depuis l'API\n    async fetchCompetences() {\n      try {\n        const response = await axios.get(\"/gestion/competences/get\");\n        this.competences = response.data;\n      } catch (error) {\n        console.error(\"Erreur lors de la récupération des compétences :\", error);\n      }\n    },\n  },\n  async mounted() {\n    // Récupère les compétences au montage\n    await this.fetchCompetences();\n  },\n};\n</script>\n\n<style scoped>\n.bulle-container {\n  background-color: #fff;\n  box-shadow: 0px 2px 15px lightgray;\n  padding: 20px;\n  border-radius: 8px;\n}\n\n.titre {\n  font-size: 1.5rem;\n  color: #343a40;\n}\n\n.competences-container {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 10px;\n  margin-top: 15px;\n}\n\n.competence-button {\n  padding: 10px 20px;\n  border: 1px solid #4caf50;\n  border-radius: 5px;\n  background-color: #e3f2fd;\n  color: #0066cc;\n  font-size: 1rem;\n  cursor: pointer;\n  transition: background-color 0.3s, transform 0.2s;\n  text-align: center;\n  text-decoration: none;\n}\n\n.competence-button:hover {\n  background-color: #4caf50;\n  color: #fff;\n  transform: scale(1.05);\n}\n\nul {\n  list-style: none;\n  padding: 0;\n}\n\nul li {\n  margin: 5px 0;\n  padding: 5px;\n  background-color: #f8f9fa;\n  border-radius: 4px;\n}\n</style>\n"],"sourceRoot":""}]);
